@import url(https://fonts.googleapis.com/css?family=Roboto:100,300,400,500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  background-color: $main;
  text-align: center;
}

#root {
  min-height: 100%;
  display: flex;
  position: relative;
}

.root {
  min-height: 100%;
  height: 2000px;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.root {
  flex-grow: 1;
  min-height: 100%;
  display: flex;
  justify-content: center;
  flex-direction: column;
}

.container-height {
  flex: 1 0 auto;
  flex-grow: 1;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

header {
  font-family: Roboto; }
  header .brand {
    padding: 50px 0px; }
    header .brand p {
      text-align: center;
      letter-spacing: 0.2rem;
      font-size: 60px;
      font-weight: 300;
      color: #444040; }
      header .brand p span {
        font-size: 16px;
        letter-spacing: 0rem;
        font-weight: 400; }
  header .menu {
    width: 100%;
    background-color: #e45353; }
    header .menu ul {
      width: 100%;
      padding: 0px;
      display: flex;
      justify-content: center;
      margin: 0px; }
      header .menu ul li {
        font-size: 14px;
        padding: 25px 20px;
        font-weight: 500;
        list-style: none;
        color: white;
        -webkit-transition: 0.3s;
        transition: 0.3s;
        cursor: pointer; }
        header .menu ul li:hover {
          -webkit-transform: scale(0.9);
                  transform: scale(0.9);
          background-color: white;
          color: #e45353; }
  header .button-xs {
    display: none;
    position: absolute;
    right: 0px;
    top: 0px;
    margin: 10px;
    width: 50px;
    height: 50px; }

@media (max-width: 576px) {
  header {
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    left: 0;
    width: 100%;
    z-index: 10;
    box-shadow: 0px 0px 3px 0px #cdcdcd; }
    header .brand {
      padding: 20px 0px; }
      header .brand p {
        font-size: 20px; }
        header .brand p span {
          font-size: 15px; }
    header .menu {
      display: none; }
    header .button-xs {
      display: flex;
      justify-content: center; }
      header .button-xs i {
        color: #e45353;
        font-size: 50px; } }

@media (max-width: 776px) {
  header .brand {
    padding: 20px 0px; } }

.helper-container {
  border-radius: 10px;
  margin: 5px;
  position: fixed;
  z-index: 10;
  top: 0px;
  right: 0px;
  display: flex;
  box-shadow: 0px 0px 3px 0px #cdcdcd; }
  .helper-container .helper-button {
    background-color: #e45353;
    display: flex;
    justify-content: center;
    margin-right: -180px;
    -webkit-transition: margin 0.5s;
    transition: margin 0.5s;
    word-wrap: break-all; }
    .helper-container .helper-button i {
      overflow: hidden;
      color: #723030;
      font-size: 25px;
      vertical-align: middle;
      float: right;
      margin: 10px; }
      .helper-container .helper-button i:hover {
        -webkit-transition: 0.2s;
        transition: 0.2s;
        color: #fff; }
  .helper-container .helper-button.is-open {
    margin-right: 0px; }
  .helper-container .helper {
    background-color: #e45353;
    width: auto;
    display: flex;
    flex-direction: column;
    padding: 10px 10px 10px 15px;
    border-radius: 10px 0px 0px 10px;
    justify-content: center;
    background-color: #e45353;
    text-align: center; }
    .helper-container .helper i {
      -webkit-transition: 0.5s;
      transition: 0.5s;
      color: #fff;
      font-size: 20px; }
      .helper-container .helper i:hover {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg); }

@-webkit-keyframes invise {
  100% {
    -webkit-transform: scale(0.5);
            transform: scale(0.5);
    display: none; } }

@keyframes invise {
  100% {
    -webkit-transform: scale(0.5);
            transform: scale(0.5);
    display: none; } }

@media (max-width: 776px) {
  .helper-container {
    flex-direction: column;
    width: 50px;
    left: 0; }
    .helper-container .helper-button {
      margin-right: 0px;
      margin-top: -180px;
      display: flex;
      justify-content: center;
      flex-direction: column; }
      .helper-container .helper-button i {
        text-align: center;
        float: left; }
    .helper-container .helper-button.is-open {
      margin-top: 0px !important; }
    .helper-container .helper {
      order: 1;
      padding: 10px 0px;
      border-radius: 0px 0px 10px 10px !important; }
      .helper-container .helper i {
        -webkit-transform: rotate(-90deg);
                transform: rotate(-90deg); }
        .helper-container .helper i:hover {
          -webkit-transform: rotate(270deg);
                  transform: rotate(270deg); } }

.mobile-menu-container {
  display: none;
  z-index: 100;
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  background-color: #e45353; }
  .mobile-menu-container .mobile-menu {
    display: block;
    width: 100%;
    height: 100%;
    position: relative; }
    .mobile-menu-container .mobile-menu p {
      color: #fff; }
  .mobile-menu-container .mobile-menu-button {
    position: absolute;
    right: 0;
    top: 0;
    padding: 20px;
    -webkit-transition: .2s;
    transition: .2s; }
    .mobile-menu-container .mobile-menu-button i {
      font-size: 40px; }
    .mobile-menu-container .mobile-menu-button:hover {
      color: #fff; }

.mobile-menu-container.show {
  display: block;
  overflow: hidden; }

@media (min-width: 577px) {
  .menu-container {
    display: none !important; } }

footer {
  width: 100%;
  background-color: #445469;
  color: #fff;
  box-shadow: 0px 0px 3px 0px #cdcdcd; }
  footer .footer-brand {
    margin: 50px 0px;
    font-size: 30px;
    text-shadow: 1px 1px 1px grey;
    text-align: center; }
  footer .footer-links {
    display: flex;
    justify-content: center; }
    footer .footer-links ul {
      padding: 0px;
      margin: 0px 0px 50px 0px; }
      footer .footer-links ul li {
        margin: 10px 0px;
        text-align: center;
        list-style: none; }
  footer .footer-rights {
    padding: 20px 0px;
    width: 100%;
    background-color: #2f3b49;
    text-align: center; }

